import types from './mutation-types';

const mutations = {
  [types.SET_SUBJECT_DATA] (state, data) {
    state.getSubject = data;
  },
  [types.SET_RANGE_DATA] (state, data) {
    state.getRange = data;
  },
  [types.SET_SCORE_RANGE_VALUE] (state, data) {
    state.getScoreRangeValue = data;
  },
  [types.SET_NOTICE_RANGE_VALUE] (state, data) {
    state.getNoticeRangeValue = data;
  },
  [types.SET_HOME_RANGE_VALUE] (state, data) {
    state.getHomeRangeValue = data;
  },
  [types.SET_SCORE] (state, data) {
    state.getScore = data;
  },
  [types.SET_SCORE_VALUE] (state, data) {
    state.getScoreValue = data;
  },
  [types.SET_HOMEWORK_CONTENT_DATA] (state, data) {
    state.getHomeworkContent = data;
  },
  [types.SET_HOMEWORK_IMGLIST_DATA] (state, data) {
    state.getHomewImgList = data;
  },
  [types.SET_HOMEWORK_TITLE] (state, data) {
    state.getHomeworkTitle = data;
  },
  [types.SET_EXAM_ID] (state, data) {
    state.getExamId = data;
  },
  [types.SET_NOTICE_TITLE_VALUE] (state, data) {
    state.getNoticeTitle = data;
  },
  [types.SET_NOTICE_IMTLIST_VALUE] (state, data) {
    state.getNoticeImgList = data;
  },
  [types.SET_NOTICE_CONTENT_VALUE] (state, data) {
    state.getNoticeContent = data;
  },
  [types.SET_ORIGIN_TYPE] (state, data) {
    state.getOriginType = data;
  }
};

export default mutations;



