/**
 * post 请求
 * @param url
 * @param data
 */
function postO (url, data) {
  return axios({
    method: 'post',
    // baseURL: _baseURL,
    url,
    data: data,
    timeout: 10000,
    headers: {
    // 'X-Requested-With': 'XMLHttpRequest',
    // 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
      'Content-Type': 'application/json; charset=UTF-8'
    }
  });
}
function post (url, data) {
  return axios({
    method: 'post',
    // baseURL: _baseURL,
    url,
    data: qs.stringify(data),
    timeout: 10000
  });
}
/**
 * get请求
 * @param url
 * @param params
 */
function get (url, params) {
  // debugger;
  return axios({
    method: 'get',
    // baseURL: _baseURL,
    url,
    params, // get 请求时带的参数
    timeout: 10000,
    headers: {
      'X-Requested-With': 'XMLHttpRequest'
    }
  });
}

/**
 * download请求
 * @param url
 * @param params
 */
function download (url, params) {
  let p = Object.keys(params).map((key) => {
    return encodeURIComponent(key) + '=' + encodeURIComponent(encodeURIComponent(params[key]));
  }).join('&');
  window.open(_baseURL + url + '?' + p);
}

export {post, get, postO, download};



