<template>
  <!--<v-touch tag="div" id='container' class="v-touch"-->
  <!--v-on:swiperight="swipe" v-bind:swipe-options="{ direction: 'horizontal', threshold: 400 }"-->
  <!--v-bind:touchAction="'pan-y'"-->
  <!--&gt;-->
  <!--v-bind:touchAction="'pan-y'"-->
  <div class="app">
    <transition :name="trans">
      <keep-alive>
        <router-view  v-if="$route.meta.keepAlive"></router-view>
      </keep-alive>
    </transition>
    <transition :name="trans">
      <router-view v-if="!$route.meta.keepAlive"></router-view>
    </transition>
  </div>
  <!--<transition :name="trans">-->
  <!--<router-view v-if="!$route.meta.keepAlive"></router-view>-->
  <!--</transition>-->
  <!--</v-touch>-->
  <!--<router-view v-wechat-title="$route.meta.title"></router-view>-->
</template>

<script type="text/ecmascript-6">
  require('../static/js/vendor/velocity.min'); // get velocity.min

  export default {
    data () {
      return {
        trans: 'fade'
      };
    },

    created () {
      console.log('App created... ');
    },

    computed: {},

    watch: {
      '$store.state.status.loading' (val) {
        if (this.loadingObj && val) {
          this.loadingObj.style.display = 'block';
        } else if (this.loadingObj && !val) {
          this.loadingObj.style.display = 'none';
        }
      },
      '$route.name' (to, from) {
        // 根据时间戳，判断是前进还是返回
        if (!this.$route.query.timestamp) {
          this.trans = 'fade';
        } else if (!this.$router.direct()) {
          this.trans = 'slide-right';
        } else {
          this.trans = 'slide-left';
        }
        console.log(`app.${this._uid}.watch: $route!!!`, to, from, this.trans);
      }
    },

    mounted () {
      console.log('App mounted... ');
      this.loadingObj = document.getElementById('loading-wrapper');
    },

    methods: {
      /**
       * 手动transition动画
       * 默认的动画会影响keep-alive的scrollBehavior计算，原因在于计算时动画渲染还未完成，获取不到scroll，所以window.scrollTo无法完成
       *              -- Author by Dio Zhu. on 2017.3.6
       */
      beforeEnter (el) {
        this.$logger.log('app.transition.beforeEnter: ');
        el.style.opacity = 0;
      },
      enter (el, done) {
        this.$logger.log('app.transition.enter: ');
        Velocity(el, {opacity: 1}, {duration: 500}, {complete: this.entered(done)}); //eslint-disable-line
      },
      entered (done) {
        done();
        this.$logger.log('app.transition.entered!!!');
      },
      // 此回调函数是可选项的设置
      // 与 CSS 结合时使用
      leave (el, done) {
        this.$logger.log('app.transition.leave: ');
        el.style.opacity = 0;
        done();
      }
    }
  };
</script>

<style rel="stylesheet/scss" lang="scss">
  @import './scss/app.scss'; // 基础样式
  // @import "../node_modules/highlight.js/styles/zenburn.css"; // 代码高亮的样式，正式项目请移除: darcula、googlecode
  // @import "./scss/_highlight.scss"; // 代码高亮的样式，正式项目请移除

  [v-cloak] {
    display: none;
  }
  .app{
    width: 100%;
    height: 100%;
  }
  .v-touch {
    height: 100%;
    position: relative;
    /*touch-action: pan-x pan-y !important;*/

    .page {
      height: 100%;
      position: relative;
      overflow: auto; // 设定默认滚动容器
      -webkit-overflow-scrolling: touch;
      /*touch-action: pan-x pan-y !important;*/
      /*touch-action: pan-y !important;*/
      /*touch-action: pan-x pan-y !important;*/
      /*.v-scroll-container {*/
      /*height: 100%;*/
      /*overflow: auto;*/
      /*}*/
    }
  }

  // vue-lazyload 延时加载样式
  img[lazy=loading] {
    width: pxTorem(20px);
    height: pxTorem(20px);
    /*margin: pxTorem(100px) 0;*/
  }

  img[lazy=loaded] {
    width: 100%;
    height: 100%;
    /*margin: 0 0 pxTorem(3px) 0;*/
  }

  img[lazy=error] {
    width: pxTorem(34px);
    height: pxTorem(28px);
    /*margin: pxTorem(100px) 0;*/
  }

  .page {

    h2 {
      margin-top: pxTorem(15px);
      margin-left: pxTorem(15px);
      line-height: pxTorem(40px);
    }

    .desc {
      /*height: pxTorem(28px);*/
      font-size: pxTorem(14px);
      line-height: 1.5;
      padding: 0 pxTorem(15px);
      color: #777E8C;
      /*background: #f2f2f4;*/

      /*display: table; // v-scroller会造成盒模型崩塌?*/
      table-layout: fixed; // 不加上这个如果内部标签有一大串url, 此父级会被撑开
      text-align: justify; // 两边对齐...
      word-break: break-all; // 强制换行, 避免超长url
      white-space: pre-wrap;
    }

    ul.desc {
      width: 100%;

      li {
        padding: pxTorem(5px);
        background: rgba(86, 61, 124, 0.15);
        border: 1px solid rgba(86, 61, 124, 0.2);
      }
    }

    .attributes {
      /*padding: 0 pxTorem(6px);*/
    }
  }

  /* 滑入滑出动画。 Author by Dio Zhu. on 2017.3.28 */
  .slide-left-enter-active, .slide-right-enter-active {
    transition: all .3s ease;
  }

  .slide-left-leave-active, .slide-right-leave-active {
    /*transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);*/
    transition: all .1s ease;
  }

  .slide-left-enter, .slide-left-leave-active {
    transform: translateX(20px);
    opacity: 0;
  }

  .slide-right-enter, .slide-right-leave-active {
    transform: translateX(-20px);
    opacity: 0;
  }
</style>



