import {get, post, postO} from './http';
// import config from '@/config';

const api = {
  /**
   * 发布通知(包含通知、作业、成绩)
   */
  publishNotice: (obj) => {
    return postO('/jyt-school-interaction/jxhd/notice/publish', obj);
  },
  /**
   * 回执通知
   */
  receiptNotice: (obj) => {
    return post('/jyt-school-interaction/jxhd/notice/receipt', obj);
  },
  /**
   *通知一键全部提醒(已提醒的除外
   */
  remaindAll: (obj) => {
    return post('/jyt-school-interaction/jxhd/notice/remind/all', obj);
  },
  /**
   *通知个人提醒
   */
  remaindSingle: (obj) => {
    return post('/jyt-school-interaction/jxhd/notice/remind/single', obj);
  },
  /**
   * 根据筛选条件获取通知列表(我发布的)
   * @param obj
   * @returns {*}
   */
  getPublisher: (obj) => {
    return get('/jyt-school-interaction/jxhd/notice/list/publisher', obj);
  },
  /**
   * 根据筛选条件获取通知列表(我接收的)
   * @param obj
   * @returns {*}
   */
  getInformReceive: (obj) => {
    return get('/jyt-school-interaction/jxhd/notice/list/receiver', obj);
  },
  /**
   *通知发布概览查看(接收人查看)
   * @param obj
   */
  getReveiver: (obj) => {
    return get('/jyt-school-interaction/jxhd/notice/notice/reveiver', obj);
  },
  /**
   * 通知发布详情查看(发布人查看且需回执)
   * @param obj
   * @returns {*}
   */
  getWithReceipt: (obj) => {
    return get('/jyt-school-interaction/jxhd/notice/noticeDetail/publisher/withReceipt', obj);
  },
  /**
   * 通知发布详情查看(发布人查看且无需回执)
   * @param obj
   * @returns {*}
   */
  getWithOutReceipt: (obj) => {
    return get('/jyt-school-interaction/jxhd/notice/noticeDetail/publisher/withoutReceipt', obj);
  },
  /**
   * 通知发布详情查看(接收人查看)
   * @param obj
   * @returns {*}
   */
  getReceiveList: (obj) => {
    return get('/jyt-school-interaction/jxhd/notice/noticeDetail/reveiver', obj);
  },
  /**
   *历史成绩查询接口
   * @param obj
   * @returns {*}
   */
  getHistoryScore: (obj) => {
    return post('/jyt-school-interaction/jxhd/achievement/findHistoryScore', obj);
  },
  /**
   *成绩详情
   * @param obj
   * @returns {*}
   */
  getScoreDetail: (obj) => {
    return get('/jyt-school-interaction/jxhd/notice/noticeDetail/reveiver/score', obj);
  },
  /**
  * 发布范围
  * */
  getRange: (obj) => {
    return get('/jyt-school-interaction/jxhd/notice/noticeRange/list', obj);
  },
  /*
  /**
   * 考试成绩范围
   * */
  getFindExamList: (obj) => {
    return post('/jyt-school-interaction/jxhd/exam/findExamListByApp', obj);
  },
  /**
   * 获取筛选班级列表
   * @param obj
   * @returns {*}
   */
  getClassList: (obj) => {
    return get('/jyt-school-interaction/jxhd/notice/classes/list', obj);
  },
  /**
   * 获取用户登录信息
   * @param obj
   * @returns {*}
   */
  getLogin: (obj) => {
    return post('/jyt-school-interaction/loginAppWithCode', obj);
  },
  /**
   * 获取科目
   * @returns {*}
   */
  getSubjects: (obj) => {
    return get('/jyt-school-interaction/jxhd/notice/subjects/list', obj);
  }
};
export default api;



