ames slideInRight {\nfrom {\n    -webkit-transform: translate3d(100%, 0, 0);\n    transform: translate3d(100%, 0, 0);\n    visibility: visible;\n}\nto {\n    -webkit-transform: translate3d(0, 0, 0);\n    transform: translate3d(0, 0, 0);\n}\n}\n@keyframes slideInRight {\nfrom {\n    -webkit-transform: translate3d(100%, 0, 0);\n    transform: translate3d(100%, 0, 0);\n    visibility: visible;\n}\nto {\n    -webkit-transform: translate3d(0, 0, 0);\n    transform: translate3d(0, 0, 0);\n}\n}\n.slideInRight {\n  -webkit-animation-name: slideInRight;\n  animation-name: slideInRight;\n}\n@-webkit-keyframes slideOutRight {\nfrom {\n    -webkit-transform: translate3d(0, 0, 0);\n    transform: translate3d(0, 0, 0);\n}\nto {\n    visibility: hidden;\n    -webkit-transform: translate3d(100%, 0, 0);\n    transform: translate3d(100%, 0, 0);\n}\n}\n@keyframes slideOutRight {\nfrom {\n    -webkit-transform: translate3d(0, 0, 0);\n    transform: translate3d(0, 0, 0);\n}\nto {\n    visibility: hidden;\n    -webkit-transform: translate3d(100%, 0, 0);\n    transform: translate3d(100%, 0, 0);\n}\n}\n.slideOutRight {\n  -webkit-animation-name: slideOutRight;\n  animation-name: slideOutRight;\n}\n.v-touch {\n  height: 100%;\n  position: relative;\n  /*touch-action: pan-x pan-y !important;*/\n}\n.v-touch .page {\n  height: 100%;\n  position: relative;\n  overflow-x: hidden;\n  overflow-y: auto;\n  -webkit-overflow-scrolling: touch;\n  background: #F2f2f2;\n}\n\n/**\r\n * 加下面这两段样式是为了配合v-scroll、v-refresh组件，在被引入的页面进行滚动容器的设定，其他页面还原基本样式。\r\n * 目的是为了让输入页面input获取焦点时，键盘弹出的状态，使得页面滚动容器的滚动条不冲突。\r\n * QA在物业通-报修项目中新增报修时发现input获取焦点后上下滚动卡顿，实是滚动容器在输入法键盘弹出后出现了两个滚动条；\r\n * 另一个目的，如果滚动容器为默认的window的话，input获取焦点后，会把input顶上去，如果使用非window的滚动容器则不行；\r\n *              -- Author by Dio Zhu. on 2017.8.8\r\n */\n\n/* loading */\n.loading-wrapper {\n  position: fixed;\n  left: 0;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  z-index: 10000;\n  background: #FFF;\n  opacity: 0.8;\n}\n.loading-wrapper.show {\n  display: block;\n}\n.loading-wrapper.hide {\n  display: none;\n}\n.loading-wrapper .loading-img {\n  position: absolute;\n  width: 0.8rem;\n  height: 0.8rem;\n  top: 45%;\n  left: 50%;\n  margin-top: -0.4rem;\n  margin-left: -0.4rem;\n  text-align: center;\n}\n\n/* 可以设置不同的进入和离开动画 */\n\n/* 设置持续时间和动画函数 */\n.fade-enter-active {\n  -webkit-transition: opacity .3s ease;\n  transition: opacity .3s ease;\n}\n.fade-enter,\n.fade-leave-active {\n  opacity: 0;\n}\n\n/***所有页面共有样式***/\n.page-all {\n  width: 100%;\n  overflow-x: hidden;\n  overflow-y: auto;\n  -webkit-overflow-scrolling: touch;\n  -ms-touch-action: pan-y;\n  touch-action: pan-y;\n}\n.page-all h2 {\n  padding: 0.26667rem 0.53333rem;\n}\n.no-border {\n  border: 0 !important;\n}\n.page {\n  width: 100%;\n}\n\n/* 边框样式----------------------------begin */\n.border-bottom-1px,\n.border-left-1px,\n.border-right-1px,\n.border-top-1px {\n  position: relative;\n}\n.border-bottom-1px:after,\n.border-bottom-1px:before,\n.border-left-1px:after,\n.border-left-1px:before,\n.border-right-1px:after,\n.border-right-1px:before,\n.border-top-1px:after,\n.border-top-1px:before {\n  content: \"\";\n  display: block;\n  position: absolute;\n  -webkit-transform-origin: 0 0;\n  transform-origin: 0 0;\n}\n.border-top-1px:before {\n  border-top: 0.026667rem solid #DDDEE3;\n  left: 0;\n  top: 0;\n  width: 100%;\n  -webkit-transform-origin: 0 top;\n  transform-origin: 0 top;\n}\n.border-right-1px:after {\n  border-right: 0.026667rem solid #DDDEE3;\n  top: 0;\n  right: 0;\n  height: 100%;\n  -webkit-transform-origin: right 0;\n  transform-origin: right 0;\n}\n.border-bottom-1px:after {\n  border-bottom: 0.026667rem solid #DDDEE3;\n  left: 0;\n  bottom: 0;\n  width: 100%;\n  -webkit-transform-origin: 0 bottom;\n  transform-origin: 0 bottom;\n}\n.border-left-1px:before {\n  border-left: 0.026667rem solid #DDDEE3;\n  top: 0;\n  left: 0;\n  height: 100%;\n  -webkit-transform-origin: left 0;\n  transform-origin: left 0;\n}\n@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {\n.border-top-1px:before {\n    width: 200%;\n}\n.border-right-1px:after,\n  .border-top-1px:before {\n    -webkit-transform: scale(0.5) translateZ(0);\n    transform: scale(0.5) translateZ(0);\n}\n.border-right-1px:after {\n    height: 200%;\n}\n.border-bottom-1px:after {\n    width: 200%;\n}\n.border-bottom-1px:after,\n  .border-left-1px:before {\n    -webkit-transform: scale(0.5) translateZ(0);\n    transform: scale(0.5) translateZ(0);\n}\n.border-left-1px:before {\n    height: 200%;\n}\n}\n@media (-webkit-min-device-pixel-ratio: 3), (min-resolution: 3dppx) {\n.border-top-1px:before {\n    width: 300%;\n}\n.border-right-1px:after,\n  .border-top-1px:before {\n    -webkit-transform: scale(0.333) translateZ(0);\n    transform: scale(0.333) translateZ(0);\n}\n.border-right-1px:after {\n    height: 300%;\n}\n.border-bottom-1px:after {\n    width: 300%;\n}\n.border-bottom-1px:after,\n  .border-left-1px:before {\n    -webkit-transform: scale(0.333) translateZ(0);\n    transform: scale(0.333) translateZ(0);\n}\n.border-left-1px:before {\n    height: 300%;\n}\n}\n\n/* 边框样式----------------------------end */\n[v-cloak] {\n  display: none;\n}\n.app {\n  width: 100%;\n  height: 100%;\n}\n.v-touch {\n  height: 100%;\n  position: relative;\n  /*touch-action: pan-x pan-y !important;*/\n}\n.v-touch .page {\n  height: 100%;\n  position: relative;\n  overflow: auto;\n  -webkit-overflow-scrolling: touch;\n  /*touch-action: pan-x pan-y !important;*/\n  /*touch-action: pan-y !important;*/\n  /*touch-action: pan-x pan-y !important;*/\n  /*.v-scroll-container {*/\n  /*height: 100%;*/\n  /*overflow: auto;*/\n  /*}*/\n}\nimg[lazy=loading] {\n  width: 0.53333rem;\n  height: 0.53333rem;\n  /*margin: pxTorem(100px) 0;*/\n}\nimg[lazy=loaded] {\n  width: 100%;\n  height: 100%;\n  /*margin: 0 0 pxTorem(3px) 0;*/\n}\nimg[lazy=error] {\n  width: 0.90667rem;\n  height: 0.74667rem;\n  /*margin: pxTorem(100px) 0;*/\n}\n.page h2 {\n  margin-top: 0.4rem;\n  margin-left: 0