// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';
import router from './router';
import Tree from 'element-ui/lib/tree';
import 'element-ui/lib/theme-chalk/index.css';
import Comb from './vendor/index';
import store from './store';
import VueWechatTitle from 'vue-wechat-title';
import config from './config';
// import axios from 'axios';
import api from './utils/api';
import toongine from 'toongine';

Vue.use(VueWechatTitle);
// 注册蜂巢（Comb）组件
Vue.use(Comb);
Vue.use(Tree);

Vue.config.productionTip = false;
router.setTitle = function (title) {
  toongine.page.setNavigationBarTitle({
    params: {
      'title': title
    },
    callback: res => {}
  });
  // document.title = title;
  // try {
  //   setTitle(title); //eslint-disable-line
  // } catch (e) {
  // }
};

/**
 * 获取用户登录信息
 */
router.beforeEach((to, from, next) => {
  if (!config.userType) {
    if (to.meta.requiresAuth) {
      console.log('***********', !config.userType);
      router.replace({path: '/error'});
    }
    next();
  } else {
    let queryString = JSON.parse(decodeURIComponent(config.userType));
    let uType = queryString.userType;
    let userTypeObj = {
      'student': 0,
      'teacher': 2,
      'parent': 1
    };
    let userTypeNum = userTypeObj[uType];
    let params = {
      toonCode: '', // 从URL中获取toonCode
      // toonCode: config.CODE, // 从URL中获取toonCode
      userType: userTypeNum // 从URL中获取userType
    };
    let isLogin = sessionStorage.getItem('isLogin');
    if (isLogin === 'false' || isLogin === null) {
      toongine.open.getCode({
        callback: res => {
          params.toonCode = res.data;
          api.getLogin(params).then((res) => {
            if (res.data.code === 0) {
              // 获取当前教师类型 0 任课老师  1 班主任
              config.teacherType = res.data.data;
              sessionStorage.setItem('isLogin', 'true');
              if (config.queryParams.from === 'notify') {
                next({
                  path: config.queryParams.noType === '2' ? '/scoreDetail' : '/receiveDetails',
                  query: {
                    noId: config.queryParams.noId
                  }
                });
              } else {
                next();
              }
            }
          }, (err) => {
            console.log(err);
          });
        }
      });
      // 登录api
      // api.getLogin(params).then((res) => {
      //   if (res.data.code === 0) {
      //     config.teacherType = res.data.data;
      //     sessionStorage.setItem('isLogin', 'true');
      //   }
      // });
    } else {
      next();
    }
  }
});

router.afterEach(route => {
  /** set title */
  if (route.name === 'notifyList') {
    router.setTitle('班级通知');
  } else if (route.name === 'receiveDetails') {
    router.setTitle('接收详情');
  } else if (route.name === 'searchResult') {
    router.setTitle('发布范围');
  } else if (route.name === 'testScore') {
    router.setTitle('考试成绩');
  } else if (route.name === 'publishScore') {
    router.setTitle('发布成绩');
  } else if (route.name === 'publishHomework') {
    router.setTitle('发布作业');
  } else if (route.name === 'homeworkSubject') {
    router.setTitle('作业科目');
  } else if (route.name === 'publishNotice') {
    router.setTitle('发布通知');
  } else if (route.name === 'informDetails' && route.query.noType === 0) {
    router.setTitle('通知详情');
  } else if (route.name === 'informDetails' && route.query.noType === 1) {
    router.setTitle('作业详情');
  } else if (route.name === 'informDetails' && route.query.noType === 2) {
    router.setTitle('成绩详情');
  } else if (route.name === 'historyScore') {
    router.setTitle('历史成绩');
  } else if (route.name === 'scoreDetail') {
    router.setTitle('成绩详情');
  } else if (route.name === 'viewer') {
    router.setTitle('图片预览');
  }
});

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
});



