import Vue from 'vue';
import Router from 'vue-router';
// import notifyList from '@/components/notify-list.vue';
// import receiveDetails from '@/components/receive-details.vue';
// import searchResult from '@/components/search-result.vue';
// import testScore from '@/components/test-score.vue';
// import homeworkSubject from '@/components/homework-subject.vue';
// import publishScore from '@/components/publish-score.vue';
// import publishHomework from '@/components/publish-homework.vue';
// import publishNotice from '@/components/publish-notice.vue';
// import informDetails from '@/components/inform-details.vue';
// import historyScore from '@/components/history-score.vue';
// import scoreDetail from '@/components/score-detail.vue';
// import viewer from '@/vendor/v-viewer.vue';
const _import_ = file => () => import('@/components/' + file + '.vue');

Vue.use(Router);

// 时间戳转换
Vue.filter('time', function (value) {
  let time = new Date(value - 0);
  // console.log(time.getFullYear() + '-' + (time.getMonth() + 1) + '-' + time.getDate() + ' ' + time.getHours() + ':' + time.getMinutes() + ':' + time.getSeconds());
  let h = time.getHours() > 9 ? time.getHours() : '0' + time.getHours();
  let m = time.getMinutes() > 9 ? time.getMinutes() : '0' + time.getMinutes();
  // let s = time.getSeconds() > 9 ? time.getSeconds() : '0' + time.getSeconds();
  let month = (time.getMonth() + 1) > 9 ? (time.getMonth() + 1) : '0' + (time.getMonth() + 1);
  let date = time.getDate() > 9 ? time.getDate() : '0' + time.getDate();
  return month + '-' + date + ' ' + h + ':' + m;
});

// 实例化router对象
const router = new Router({
  // mode: 'hash', // 设置路由模式 可选值: 'hash' | 'history' | 'abstract'，默认'hash'
  mode: 'hash', // 设置路由模式 可选值: 'hash' | 'history' | 'abstract'，默认'hash'
  // linkActiveClass: 'u-link--Active', // 这是链接激活时的class
  // base: '/app/', // 这个是设置根目录路径，默认'/'
  base: '/app/' // 根目录路径，配合./config/index.js中的设置，目的在build后，能顺利部署到服务端，与后台文件不冲突。。。Author by Dio Zhu. on 2017.3.28
});

export default new Router({
  routes: [
    {
      path: '/',
      name: 'notifyList',
      component: _import_('notify-list'),
      meta: {title: '班级通知', requiresAuth: true, keepAlive: true}
    },
    {
      path: '/receiveDetails',
      name: 'receiveDetails',
      component: _import_('receive-details'),
      meta: {title: '接收详情', requiresAuth: true, keepAlive: false}
    },
    {
      path: '/searchResult',
      name: 'searchResult',
      component: _import_('search-result'),
      meta: {title: '发布范围', requiresAuth: true, keepAlive: true}
    },
    {
      path: '/testScore',
      name: 'testScore',
      component: _import_('test-score'),
      meta: {title: '考试成绩', requiresAuth: true, keepAlive: true}
    },
    {
      path: '/homeworkSubject',
      name: 'homeworkSubject',
      component: _import_('homework-subject'),
      meta: {title: '作业科目', requiresAuth: true, keepAlive: true}
    },
    {
      path: '/publishScore',
      name: 'publishScore',
      component: _import_('publish-score'),
      meta: {title: '发布成绩', requiresAuth: true, keepAlive: true}
    },
    {
      path: '/publishHomework',
      name: 'publishHomework',
      component: _import_('publish-homework'),
      meta: {title: '发布作业', requiresAuth: true, keepAlive: true}
    },
    {
      path: '/publishNotice',
      name: 'publishNotice',
      component: _import_('publish-notice'),
      meta: {title: '发布通知', requiresAuth: true, keepAlive: true}
    },
    {
      path: '/informDetails',
      name: 'informDetails',
      component: _import_('inform-details'),
      meta: {title: '通知详情', requiresAuth: true, keepAlive: false}
    },
    {
      path: '/historyScore',
      name: 'historyScore',
      component: _import_('history-score'),
      meta: {title: '历史成绩', historyScore: true, keepAlive: false}
    },
    {
      path: '/scoreDetail',
      name: 'scoreDetail',
      component: _import_('score-detail'),
      meta: {title: '成绩详情', historyScore: true, keepAlive: false}
    },
    {
      path: '/viewer',
      name: 'viewer',
      component: _import_('v-viewer'),
      meta: {title: '图片预览', historyScore: true, keepAlive: false}
    },
    {
      path: '/error',
      name: 'error',
      component: _import_('error'),
      meta: {title: '加载中...', requiresAuth: false}
    }
  ],
  router: router
});



